.doings{
    padding-right: 20px;
    padding-left: 20px;
    text-align: center;
    }
    
    .doings-title{
    padding-bottom: 30px;
    color: var(--txt-dark);
    @include font (800, 20px, 1.3, 0.03em);
    }
    
    .doings-item{
        height: 280px;
        background: var(--b-white);
        box-shadow: 0px 8px 26px rgba(0, 0, 0, 0.1);
        margin-bottom: 15px;
    }
    
    .doings-item:last-child{
       margin-bottom: 0;
    }

    .doings-item:hover,
    .doings-item:focus{
     background-color: var(--b-accent-background);
     color: var(--txt-light);
    }

    .doings-item:hover .doings-svg,
    .doings-item:focus .doings-svg{
      fill: var(--white);
    }

    .doings-item:hover .doings-description,
    .doings-item:focus .doings-description{
      color: var(--txt-light);
    }

    .doings-item:hover .doings-list-title::after,
    .doings-item:focus .doings-list-title::after{
      background-color: var(--b-white);
    }
    
    .doings-list-title{
        position: relative;
        text-align: center;
        @include font (700, 14px, 1.428, 0.08em);
        padding: 0 9px 42px;
    }

    .doings-list-title::after{
        content: "";
        position: absolute;
        width: 20px;
        height: 2px;
        bottom: 21px;
        left: 50%;
        transform: translate(-50%);
        background-color: var(--b-accent-background);
    }
    
    .doings-description{
        padding-bottom: 22px;
        color: var(--txt-grey);
        @include font (500, 12px, 1.666, 0.08em);
        padding: 0 7px;
    }

    .doings-svg{
        margin-top: 30px;
        margin-bottom: 20px;
        fill: var(--light);
    }  
    
    @media screen and(min-width:768px) and (max-width:1199px){

        .doings-title{
            @include font (800, 26px, 1.3, 0.03em);
            }

            .doings-list{
                display: flex;
                flex-wrap: wrap;
                justify-content: center;
            }

            .doings-item:nth-child(2n + 1){
                margin-right: 20px;
            }

            .doings-item{
                margin-bottom: 27px;
                width: 334px;
                height: 334px;
            }

            .doings-item:nth-last-child(-n + 2){
                margin-bottom: 0;
            }

            .doings-svg{
                margin-top: 40px;
                margin-bottom: 30px;
            } 

            .doings-list-title{
                padding: 0 26px 52px;
            }

            .doings-list-title::after{
                bottom: 26px;
                left: 50%;
                transform: translate(-50%);
            }

            .doings-description{
                padding: 0 22px;
            }
    }

    @media screen and(min-width:1200px){

        .doings-title{
          text-align: start;
          padding-bottom: 50px;
          margin-left: 40px;
          @include font (800, 46px, 1.3, 0.03em);
          }
  
          .doings-list{
              display: flex;
              flex-wrap: wrap;
              justify-content: center;
          }
  
          .doings-item{
              width: calc(100% / 3 - 40px);
              margin-bottom: 20px;
              max-width: 425px;
              margin-right: 20px;
              height: 396px;
          }
  
          .doings-item:nth-child(3n){
              margin-right: 0;
          }
  
          .doings-item:nth-last-child(-n + 3){
              margin-bottom: 0;
          }
  
          .doings-svg{
              margin-top: 40px;
              margin-bottom: 30px;
          } 
  
          .doings-list-title{
              padding: 0 6px 32px;
              @include font (700, 20px, 1.4, 0.08em);
          }
  
          .doings-description{
              @include font (500, 18px, 1.555, 0.08em);
              padding: 0 7px 55px;
          }
  
          .doings-list-title::after{
              display: block;
              bottom: 16px;
              left: 50%;
              transform: translate(-50%);
          }
      }